import { requireNativeView } from 'expo';

export type NamespaceProviderProps = {
  /**
   * The ID of the namespace. You can generate one with the `useId` react hook.
   */
  id: string;

  children: React.ReactNode;
};

type NativeNamespaceProviderProps = NamespaceProviderProps;

const NativeNamespaceProviderView: React.ComponentType<NativeNamespaceProviderProps> =
  requireNativeView('ExpoUI', 'NamespaceProvider');

export function NamespaceProvider(props: NamespaceProviderProps) {
  return <NativeNamespaceProviderView {...props} />;
}
